<template>
  <b-container class="bv-example-row mt-3">
    <b-row>
      <b-col>
        <b-alert variant="secondary" show><h3> </h3></b-alert>
      </b-col>
    </b-row>
    <b-row>
      <b-col></b-col>
      <b-col cols="8">
        <!-- <b-card class="text-center mt-3" style="max-width: 40rem" align="left">
          <b-form class="text-left">
            <b-alert show variant="danger" v-if="isLoginError"
              >   .</b-alert
            > -->
        <b-form-group label=":" label-for="userid">
          <b-form-input
            id="userid"
            v-model="user.userid"
            required
            disabled
            placeholder=" ...."
          ></b-form-input>
        </b-form-group>
        <b-form-group label=":" label-for="userpwd">
          <b-form-input
            type="password"
            id="userpwd"
            v-model="user.userpwd"
            required
            placeholder=" ...."
          ></b-form-input>
        </b-form-group>
        <b-form-group label=":" label-for="username">
          <b-form-input
            id="username"
            v-model="user.username"
            required
            placeholder=" ...."
          ></b-form-input>
        </b-form-group>

        <b-form-group label=":" label-for="email">
          <div id="emailid" class="custom-control-inline">
            <b-form-input
              id="emailid"
              v-model="emailid"
              required
              placeholder=" ...."
            ></b-form-input>
            @
            <b-form-select
              v-model="emailcom"
              :options="options"
            ></b-form-select>
          </div>
        </b-form-group>

        <b-button
          type="button"
          id="registerBtn"
          class="btn btn-outline-primary"
          @click="update"
        >
          
        </b-button>
        <b-button type="reset" class="btn btn-outline-danger"></b-button>

        <!-- </b-form>
        </b-card> -->
      </b-col>
      <b-col></b-col>
    </b-row>
  </b-container>
</template>

<script>
import { updateMember } from "@/api/member.js";
import { mapState } from "vuex";
const memberStore = "memberStore";
export default {
  name: "MemberModify",
  data() {
    return {
      user: {
        userid: "",
        userpwd: "",
        username: "",

        email: "",
      },
      emailid: "",
      emailcom: "",
      options: [
        { value: "ssafy.com", text: "" },
        { value: "naver.com", text: "" },
        { value: "kakao.com", text: "" },
        { value: "google.com", text: "" },
      ],
    };
  },
  computed: {
    ...mapState(memberStore, ["userInfo"]),
  },
  created() {
    this.user.userid = this.userInfo.userid;
    this.user.userpwd = this.userInfo.userpwd;
    this.user.username = this.userInfo.username;

    var str = this.userInfo.email.split("@");
    this.emailid = str[0];
    this.emailcom = str[1];
  },
  methods: {
    update() {
      this.user.email = this.emailid + "@" + this.emailcom;
      console.log(this.user);
      updateMember(this.user); //db

      this.$router.push({ name: "SignIn" });
    },
  },
};
</script>

<style></style>
