<template>
    <b-container v-if="deal" class="bv-example-row">
        <b-row class="mb-2 mt-1">
            <b-col
                ><b-img class="imgCol" :src="house.imgsUrl" fluid-grow></b-img
            ></b-col>
        </b-row>
        <b-row>
            <b-col>
                <b-alert show variant="info"
                    > : {{ deal.dealYear }}.{{ deal.dealMonth }}.{{
                        deal.dealDay
                    }}
                </b-alert>
            </b-col>
        </b-row>
        <b-row>
            <b-col>
                <b-alert show variant="secondary"
                    > : {{ deal.floor }}</b-alert
                >
            </b-col>
        </b-row>
        <b-row>
            <b-col>
                <b-alert show variant="warning"
                    > : {{ deal.area }} </b-alert
                >
            </b-col>
        </b-row>
        <b-row>
            <b-col>
                <b-alert show variant="danger"
                    > :
                    {{ getDealAmountKor(deal.dealAmount) }}</b-alert
                >
            </b-col>
        </b-row>
    </b-container>
</template>

<script>
/* eslint-disable */
import { mapState } from "vuex";
import { numToKr } from "vue-number-to-kor";

const dealStore = "dealStore";

export default {
    name: "DealDetail",
    computed: {
        ...mapState(dealStore, ["deal", "house"]),
    },
    filters: {
        price(value) {
            if (!value) return value;
            return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        },
    },
    methods: {
        getDealAmountKor(dealAmount) {
            //   
            let number = dealAmount.replace(/,/g, "");
            let dealAmountKor = numToKr(parseInt(number) * 10000, false);
            return dealAmountKor;
        },
    },
};
</script>

<style scoped>
.imgCol {
    height: 450px;
}
</style>
